/**
 *
 */
export default class Modal {

    /**
     *
     * @param options
     */
    constructor(options) {
        this.el = options.el;
        this.background = this.el.querySelector('.modal__background');
        this.content = this.el.querySelector('.modal__content');
        this.close = this.el.querySelector('.modal__close');

        this.hide = this.hide.bind(this);
        this.clearContent = this.clearContent.bind(this);
        this.background.addEventListener('click', this.hide);
        this.close.addEventListener('click', this.hide);

        // Enable direct access to modal
        window.modal = this;
    }

    /**
     *
     */
   show() {
       this.el.classList.toggle('modal--visible', true);
   }

    /**
     *
     */
   hide() {
       if(this.el.classList.contains('modal--visible')) {
           this.el.classList.toggle('modal--visible', false);
           setTimeout(this.clearContent, 300);
       }
   }

    /**
     *
     */
   clearContent() {
       this.content.innerHTML = null;
   }

    /**
     *
     * @param html
     */
   setContent(html) {
       this.content.innerHTML = html;
   }

}