import ViewMap from './views/ViewMap';

import MainMenu from './views/MainMenu';

import Videoplayer from './views/Videoplayer';
import FileUpload from './views/FileUpload';

import Modal from './views/Modal';
import LazyImage from './views/LazyImage';
import HeroWidget from './views/HeroWidget';

import HowItWorksButton from './views/HowItWorksButton';
import OverviewCard from './views/OverviewCard';
import AppointmentFormWidget from './views/AppointmentFormWidget';
import ContactButton from './views/ContactButton';

export default class Application {

    constructor() {
        this.views = new ViewMap(this);
        this.mainMenu = new MainMenu();

        this.views.map('.video-player', Videoplayer);
        this.views.map('.file-upload', FileUpload);
        this.views.map('.modal', Modal);

        this.views.map('img[loading="lazy"]', LazyImage);

        this.views.map('.hero-widget', HeroWidget);
        this.views.map('.how-it-works-button', HowItWorksButton);
        this.views.map('.overview-card', OverviewCard);
        this.views.map('.appointment-form-widget', AppointmentFormWidget);
        this.views.map('.contact.menu-link', ContactButton);

        this.views.build();
    }
}