export default class HeroWidget {

    /**
     *
     * @param options
     */
    constructor(options) {
        this.el = options.el;

        this.image = this.el.querySelector('.image');
        this.video = this.el.querySelector('.video');
        this.iframe = this.el.querySelector('iframe');
        this.title = this.el.querySelector('h1');

        this.ctaButton = this.el.querySelector('.cta-button');
        this.target = document.querySelector('#contact');

        this.onResize = this.onResize.bind(this);
        this.onScroll = this.onScroll.bind(this);
        this.update = this.update.bind(this);
        this.onPlayClick = this.onPlayClick.bind(this);
        this.onCTAClick = this.onCTAClick.bind(this);
        this.onHeroVideoPlaying = this.onHeroVideoPlaying.bind(this);
        this.resizeIFrame = this.resizeIFrame.bind(this);

        if(this.video) {
            this.player = new Vimeo.Player(this.iframe);
            this.player.on('loaded', this.onHeroVideoPlaying);
            this.player.on('play', () => {
                this.video.classList.add('video--playing');
            });

        }

        window.addEventListener('resize', this.onResize);
        document.addEventListener('scroll', this.onScroll);
        this.image.addEventListener('load', this.onResize);

        if(this.target) {
            this.ctaButton.addEventListener('click', this.onCTAClick);
        }

        this.onResize();
    }

    /**
     *
     */
    onHeroVideoPlaying() {
        console.log('onHeroVideoPlaying', this.player);
    }

    /**
     *
     */
    onScroll() {
        this.update();
    }

    /**
     *
     */
    onResize() {
        this.update();
    }

    /**
     *
     */
    update() {
        if(this.player) {
            Promise.all([this.player.getVideoWidth(), this.player.getVideoHeight()]).then(this.resizeIFrame);
        }
    }

    resizeIFrame(dimensions) {
        var vw = dimensions[0];
        var vh = dimensions[1];
        var cw = this.video.clientWidth;
        var ch = this.video.clientHeight;

        var s = ch / vh;

        var iw = Math.ceil(vw * s);
        var ih =  Math.ceil(vh * s);

        console.log(iw, ih);

        this.iframe.style.width = iw + 'px';
        this.iframe.style.height = ih + 'px';
        this.iframe.style.transform = 'translateX(-' + Math.round((iw - cw) / 2) + 'px)';
    }


    /**
     *
     */
    onPlayClick() {
        console.log('onPlayClick');

        var id = '{' + this.vimeoId + '}';
        window.modal.show();
        window.modal.setContent('<div class="modal-video" id="' + id + '"></div');

        var options = {
            id: this.vimeoId,
            title: false,
            byline: false,
            controls: true,
            autoplay: true,
            color: 'E2F8F9'
        };
        this.howPlayer = new Vimeo.Player(id, options);
        this.howPlayer.on('loaded', this.onIframeLoaded);
    }

    /**
     *
     */
    onIframeLoaded() {
        console.log('onIframeLoaded');

        var modalVideo = document.querySelector('.modal .modal-video');
        modalVideo.classList.add('modal-video--loaded');

        if(window.__vimeoRefresh) {
            window.__vimeoRefresh();
        }

        this.onResize();
    }

    onCTAClick(event) {
        event.preventDefault();
        window.scrollTo({top: this.target.documentOffsetTop, behavior: 'smooth'});
    }
}