export default class OverviewCard {

    /**
     *
     * @param options
     */
    constructor(options) {

        this.el = options.el;
        this.toggleButton = this.el.querySelector('.overview-card__toggle');

        this.onToggleClick = this.onToggleClick.bind(this);
        this.toggleButton.addEventListener('click', this.onToggleClick);

        this.onResize();
    }

    /**
     *
     */
    onScroll() {
        this.update();
    }

    /**
     *
     */
    onResize() {
        this.update();
    }

    /**
     *
     */
    update() {
    }

    /**
     *
     */
    onToggleClick() {
        this.el.classList.toggle('overview-card--expanded');
    }
}