export default class ContactButton {

    /**
     *
     * @param options
     */
    constructor(options) {
        this.el = options.el;

        this.target = document.querySelector('#contact');
        this.onClick = this.onClick.bind(this);

        if(this.target) {
            this.el.classList.toggle('contact--visible', true);
            this.el.addEventListener('click', this.onClick);
        }
    }

    onClick(event) {
        event.preventDefault();
        window.scrollTo({top: this.target.documentOffsetTop, behavior: 'smooth'});
    }
}