export default class HowItWorksButton {

    /**
     *
     * @param options
     */
    constructor(options) {
        console.log('HowItWorksButton', options.el, options.el.dataset);

        this.el = options.el;
        this.vimeoID = this.el.dataset['vimeoid'];

        this.onPlayClick = this.onPlayClick.bind(this);
        this.el.addEventListener('click', this.onPlayClick);

        this.onResize();
    }

    /**
     *
     */
    onScroll() {
        this.update();
    }

    /**
     *
     */
    onResize() {
        this.update();
    }

    /**
     *
     */
    update() {
    }

    /**
     *
     */
    onPlayClick() {
        console.log('onPlayClick', this.vimeoID);

        var id = '{' + this.vimeoID + '}';
        window.modal.show();
        window.modal.setContent('<div class="modal-video" id="' + id + '"></div');

        var options = {
            id: this.vimeoID,
            title: false,
            byline: false,
            controls: true,
            autoplay: true,
            color: 'E2F8F9'
        };
        this.player = new Vimeo.Player(id, options);
        this.player.on('loaded', this.onIframeLoaded);
    }

    /**
     *
     */
    onIframeLoaded() {
        var modalVideo = document.querySelector('.modal .modal-video');
        modalVideo.classList.add('modal-video--loaded');

        if(window.__vimeoRefresh) {
            window.__vimeoRefresh();
        }

        this.onResize();
    }
}