import Cookies from 'js-cookie';

// GDRP
document.addEventListener('DOMContentLoaded', function () {
    var i = 0;
    try {
        var preferencesElements = document.querySelectorAll('.cookie-preferences');
        for(i=0 ; i<preferencesElements.length ; i++) {
            preferencesElements[i].addEventListener('click', showCookies);
        }
    } catch(e) {

    }

    var acceptElements = document.querySelectorAll('.gdpr__accept');
    for(i=0 ; i<acceptElements.length ; i++) {
        acceptElements[i].addEventListener('click', updateCookieSettings);
    }

    var declineElements = document.querySelectorAll('.gdpr__decline');
    for(i=0 ; i<declineElements.length ; i++) {
        declineElements[i].addEventListener('click', declineCookieSettings);
    }
});

/**
 *
 */
function updateCookieSettings() {
    console.log('updateCookieSettings');
    // var preferences = $('#gdpr-preferences').prop('checked');
    var statistics = !!document.querySelector('#gdpr-statistics').checked;
    var marketing = !!document.querySelector('#gdpr-marketing').checked;
    var duration = {expires: 1825};

    Cookies.set('preferences', 1, duration);
    Cookies.set('statistics', statistics ? 1 : 0, duration);
    Cookies.set('marketing', marketing ? 1 : 0, duration);

    Cookies.remove('_ga');
    Cookies.remove('_fb');
    Cookies.remove('_fbp');

    location.reload();
}

/**
 *
 */
function declineCookieSettings() {
    console.log('declineCookieSettings');
    // var preferences = $('#gdpr-preferences').prop('checked');
    var statistics = false;
    var marketing = false;
    var duration = {expires: 1825};

    Cookies.set('preferences', 1, duration);
    Cookies.set('statistics', statistics ? 1 : 0, duration);
    Cookies.set('marketing', marketing ? 1 : 0, duration);

    Cookies.remove('_ga');
    Cookies.remove('_fb');
    Cookies.remove('_fbp');

    location.reload();
}

/**
 *
 */
function showCookies(e) {
    console.log('showCookies');
    e.preventDefault();
    document.querySelector('.gdpr-panel').classList.toggle('gdpr-panel--hidden', false);
}