export default class Videoplayer {

    /**
     *
     * @param options
     */
    constructor(options) {
        this.el = options.el;
        this.wrapper = options.el.querySelector('.video-player__video-wrapper');

        this.vimeoId = this.el.dataset['vimeoid'];
        this.youtubeId = this.el.dataset['youtubeid'];

        this.image = this.el.querySelector('.image');
        this.iframe = this.el.querySelector('iframe');
        this.playButton = this.el.querySelector('.video-player__play-button');

        this.onLoadIframe = this.onLoadIframe.bind(this);
        this.onLoadIframeLoaded = this.onLoadIframeLoaded.bind(this);
        this.onResize = this.onResize.bind(this);

        this.playButton.addEventListener('click', this.onLoadIframe);
        window.addEventListener('resize', this.onResize);
        this.image.addEventListener('load', this.onResize);

        this.onResize();
    }

    /**
     *
     */
    onLoadIframe() {
        this.el.classList.add('video-player--active');

        if(this.vimeoId) {
            this.initializeVimeo();
        }

        if(this.youtubeId) {
            this.initializeYoutube();
        }

        this.onResize();
    }

    /**
     *
     */
    onLoadIframeLoaded() {
        this.iframe = this.el.querySelector('iframe');

        if(window.__vimeoRefresh) {
            window.__vimeoRefresh();
        }

        this.onResize();
    }

    /**
     *
     */
    onResize() {
        if(this.iframe) {
            const imageRect = this.image.getBoundingClientRect();
            this.iframe.setAttribute('width', Math.floor(imageRect.width) + '');
            this.iframe.setAttribute('height', Math.floor(imageRect.height) + '');
        }
    }

    /**
     *
     */
    initializeVimeo() {
        var id = '{' + this.vimeoId + '}';
        var options = {
            id: this.vimeoId,
            title: false,
            byline: false,
            controls: true,
            autoplay: true,
            color: 'E2F8F9'
        };
        this.player = new Vimeo.Player(id, options);
        this.player.on('loaded', this.onLoadIframeLoaded);

        this.iframe = this.el.querySelector('iframe');
        this.onResize();
    }

    /**
     *
     */
    initializeYoutube() {
        window.onYouTubeIframeAPIReady = this.onYouTubeIframeAPIReady.bind(this);

        var tag = document.createElement('script');
        tag.src = "https://www.youtube.com/iframe_api";
        var firstScriptTag = document.getElementsByTagName('script')[0];
        firstScriptTag.parentNode.insertBefore(tag, firstScriptTag);
    }

    onYouTubeIframeAPIReady() {
        var id = '{' + this.youtubeId + '}';
        this.player = new YT.Player(id, {
            height: '390',
            width: '640',
            videoId: this.youtubeId,
            playerVars: { 'autoplay': 1},
            events: {
                'onReady': () => {
                    this.player.playVideo();
                    this.onLoadIframeLoaded()
                }
            }
        });

        this.iframe = this.el.querySelector('iframe');
        this.onResize();
    }

}