import request from 'superagent';

export default class AppointmentFormWidget {

    /**
     *
     * @param options
     */
    constructor(options) {
        this.el = options.el;

        console.log('AppointmentFormWidget');

        this.onChange = this.onChange.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
        this.onSuccess = this.onSuccess.bind(this);
        this.onError = this.onError.bind(this);
        this.onTextareaChange = this.onTextareaChange.bind(this);

        this.form = this.el.querySelector('form');

        this.submitButton = this.el.querySelector('button[type="submit"]');

        this.successURL = this.el.querySelector('.success-page').dataset['successpage'];

        this.formError = this.el.querySelector('.form-error');
        this.formErrorTitle = this.el.querySelector('.form-error__title');
        this.formErrorMessage = this.el.querySelector('.form-error__message');

        this.name = this.el.querySelector('input[name="name"]');
        this.email = this.el.querySelector('input[name="email"]');
        this.phone = this.el.querySelector('input[name="phone"]');
        this.activity = this.el.querySelector('input[name="activity"]');
        this.type = this.el.querySelector('select[name="type"]');
        this.appointmentType = this.el.querySelector('input[name="appointment_type"]');
        this.message = this.el.querySelector('textarea[name="message"]');

        this.form.addEventListener('change', this.onChange);
        this.form.addEventListener('submit', this.onSubmit);
        this.message.addEventListener('input', this.onTextareaChange);

        this.update();

    }

    /**
     *
     */
    onChange(event) {
        console.log(event.target);

        var formData = new FormData(this.form);

        if(event.target === this.type) {
            event.target.classList.toggle('select--empty', false);
        }
    }

    /**
     *
     * @param event
     */
    onSubmit(event) {
        event.preventDefault();

        var formData = new FormData(this.form);

        // Honeypot value, if not empty its a bot
        const beeInputValue = formData.get('bee-input');
        if (beeInputValue && (beeInputValue + '').length > 0) {
            alert(beeInputValue);
            //
        } else {
            this.hideError();
            if (!this.sending && this.validate()) {
                this.showSending();
                const apiCall = request('POST', '/wp-content/themes/founders/email/contactForm.php');
                apiCall.send(formData).end((error, result) => {

                    // handle response
                    if (error) {
                        this.onError(error);
                    } else {
                        this.onSuccess(result.body);
                    }
                });
            }
        }
    }

    /**
     *
     */
    update() {
    }

    /**
     *
     */
    validate() {
        var formData = new FormData(this.form);

        const appointmentType = formData.get('appointment_type');
        const name = formData.get('name');
        const email = formData.get('email');
        const phone = formData.get('phone');
        const activity = formData.get('activity');
        const type = formData.get('type');
        const message = formData.get('message');

        let valid = true;
        let errorMessages = [];

        if (this._isEmpty(name)) {
            valid = false;
            errorMessages.push(this._getErrorMessage('[name="name"]'));
        }

        if (this._isEmpty(email)) {
            valid = false;
            errorMessages.push(this._getErrorMessage('[name="email"]'));
        } else if (!this._isValidEmail(email)) {
            valid = false;
            errorMessages.push(this._getErrorMessage('[name="email"]', 'errorInvalid'));
        }

        if (this._isEmpty(phone)) {
            valid = false;
            errorMessages.push(this._getErrorMessage('[name="phone"]'));
        }

        if (this._isEmpty(activity)) {
            valid = false;
            errorMessages.push(this._getErrorMessage('[name="activity"]'));
        }

        if (this._isEmpty(type)) {
            valid = false;
            errorMessages.push(this._getErrorMessage('[name="type"]'));
        }

        if (this._isEmpty(appointmentType)) {
            valid = false;
            errorMessages.push(this._getErrorMessage('[name="appointment_type"]'));
        }

        // Display errors if any
        if (errorMessages.length > 0) {
            let errorMessage = errorMessages.map((errorMessage) => {
                return `<span>${errorMessage}</span>`;
            });
            this.showError('Oeps', errorMessage.join(''));
        }

        return valid;
    }

    /**
     *
     */
    onSuccess(result) {
        window.location.href = this.successURL;
        this.hideSending();
    }

    /**
     *
     */
    onError(error) {
        this.showError('Oeps', error.message);
        this.hideSending();
    }

    /**
     *
     */
    onTextareaChange() {
    }

    /**
     *
     */
    showSending() {
        this.sending = true;
        this.form.classList.toggle('form--sending', true);

        this.el.querySelectorAll('fieldset').forEach((fieldSet) => {
            fieldSet.setAttribute('disabled', '');
        });

    }

    /**
     *
     */
    hideSending() {
        this.sending = false;
        this.form.classList.toggle('form--sending', false);

        this.el.querySelectorAll('fieldset').forEach((fieldSet) => {
            fieldSet.removeAttribute('disabled', '');
        });
    }

    /**
     *
     * @param title
     * @param errorMessage
     */
    showError(title, errorMessage) {
        this.formErrorMessage.innerHTML = errorMessage;

        if (!this.formError.classList.contains('form-error--visible')) {
            // this.formErrorTitle.innerHTML = title;
            this.formError.classList.toggle('form-error--visible', true);
        }
    }

    /**
     *
     */
    hideError() {
        if (this.formError.classList.contains('form-error--visible')) {
            this.formError.classList.toggle('form-error--visible', false);
        }
    }

    /**
     *
     * @param selector
     * @param errorType
     * @return {string | undefined}
     * @private
     */
    _getErrorMessage(selector, errorType = 'errorEmpty') {
        return this.el.querySelector(selector).dataset[errorType];
    }

    /**
     *
     * @param string
     * @private
     */
    _isEmpty(string) {
        return !string || string.trim().length === 0;
    }

    /**
     *
     * @param email
     * @return {boolean}
     * @private
     */
    _isValidEmail(email) {
        const re = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return re.test(email);
    }
}