const CONTAINS_FILE_CLASS = 'file-upload--has-file';

/**
 *
 */
export default class FileUpload {

    /**
     *
     * @param options
     */
    constructor(options) {
        this.el = options.el;
        this.input = this.el.querySelector('input');
        this.span = this.el.querySelector('span');

        this.onChange = this.onChange.bind(this);
        this.input.addEventListener('change', this.onChange);

        this.onResize();
    }

    /**
     *
     */
    onResize() {
    }

    /**
     *
     */
    onChange() {
        this.el.classList.toggle(CONTAINS_FILE_CLASS, this.input.files.length > 0);
        this.span.innerHTML = this.input.files[0].name
    }

}